console.log('index.js');
import interfaceUnit from '../../js/interfaceUnit.js'
import hexGrid from '../../js/hexGrid.js'
import mobileMenu from '../../js/mobileMenu.js'
// import Inputmask from "inputmask";
// import 'jquery.inputmask';
import $ from 'jquery'
// import 'jquery.inputmask';

// node_modules\inputmask\dist\inputmask\jquery.inputmask.js
var popup = function(){
	// console.log()
	var s = {
		tab: '.popup-tab',
		content: '.popup-content'
	}
	var tab = $(s.tab);
	var conent = $(s.window);
	var timer

	return {
		main: function(){
			var self = this;
			$(s.tab).on('mouseover', function(event){
				event.stopPropagation();
				var id = $(this).data('id');
				console.log('------------',id)
				$(window).trigger('closePopoup')
				self.showContent(id)
				$(window).trigger('hexGrid')
				clearTimeout(timer)
			})

			$('.popup-content').on('mouseover', function(event){
				event.stopPropagation();
				console.log('-------contetn')
				clearTimeout(timer)
			})

			$(window).on('mouseover', function(event){
				console.log('------- leave')
				timer = setTimeout(function(){
					$(window).trigger('closePopoup')
				}, 500)
			})
		},
		// close: function(){
		// 	$(window)
		//
		// },
		showContent: function(id){
			$('#'+id).show()
		},
		content: function(){

		},

		events: function(){
			$(window).on('closePopoup', function(){
				$(s.content).hide()
				console.log('close')
			})
		},
		init: function(){
			console.log('---------popup')
			this.main();
			this.events();
		}
	}
}

$(document).ready(function(){
	interfaceUnit().init();
	mobileMenu().init();
	hexGrid().init();
	$('#hero-slider').slick({
		infinite: true,
		dots: true,
		arrows: false,
		// centerMode: true
	})
	popup().init()
	// $('.footer__info-email').inputmask("+38(99)-999-9999");
})



